`, "",{"version":3,"sources":["webpack://./apps/settings/src/components/PasswordSection.vue"],"names":[],"mappings":";AA+EA;CACA,aAAA;CACA,sBAAA;CACA,2CAAA;CACA,2BAAA;AACA","sourcesContent":["<!--\n  - SPDX-FileCopyrightText: 2022 Nextcloud GmbH and Nextcloud contributors\n  - SPDX-License-Identifier: AGPL-3.0-or-later\n-->\n\n<script setup lang=\"ts\">\nimport axios from '@nextcloud/axios'\nimport { showError, showSuccess } from '@nextcloud/dialogs'\nimport { t } from '@nextcloud/l10n'\nimport { generateUrl } from '@nextcloud/router'\nimport { NcFormBox } from '@nextcloud/vue'\nimport { ref } from 'vue'\nimport NcButton from '@nextcloud/vue/components/NcButton'\nimport NcPasswordField from '@nextcloud/vue/components/NcPasswordField'\nimport NcSettingsSection from '@nextcloud/vue/components/NcSettingsSection'\n\nconst passwordform = ref<HTMLFormElement>()\n\nconst oldPass = ref('')\nconst newPass = ref('')\n\n/**\n * Change the user's password\n */\nasync function changePassword() {\n\tconst { data } = await axios.post(generateUrl('/settings/personal/changepassword'), {\n\t\toldpassword: oldPass.value,\n\t\tnewpassword: newPass.value,\n\t})\n\tif (data.status === 'error') {\n\t\tshowError(data.data.message)\n\t} else {\n\t\tshowSuccess(data.data.message)\n\t\toldPass.value = ''\n\t\tnewPass.value = ''\n\t\tpasswordform.value?.reset()\n\t}\n}\n</script>\n\n<template>\n\t<NcSettingsSection :name=\"t('settings', 'Password')\">\n\t\t<form\n\t\t\tref=\"passwordform\"\n\t\t\t:class=\"$style.passwordSection__form\"\n\t\t\t@submit.prevent=\"changePassword\">\n\t\t\t<NcFormBox>\n\t\t\t\t<NcPasswordField\n\t\t\t\t\tv-model=\"oldPass\"\n\t\t\t\t\t:label=\"t('settings', 'Current password')\"\n\t\t\t\t\tname=\"oldpassword\"\n\t\t\t\t\tautocomplete=\"current-password\"\n\t\t\t\t\tautocapitalize=\"none\"\n\t\t\t\t\trequired\n\t\t\t\t\tspellcheck=\"false\" />\n\n\t\t\t\t<NcPasswordField\n\t\t\t\t\tv-model=\"newPass\"\n\t\t\t\t\tcheck-password-strength\n\t\t\t\t\t:label=\"t('settings', 'New password')\"\n\t\t\t\t\t:maxlength=\"469\"\n\t\t\t\t\tname=\"newpassword\"\n\t\t\t\t\tautocomplete=\"new-password\"\n\t\t\t\t\tautocapitalize=\"none\"\n\t\t\t\t\trequired\n\t\t\t\t\tspellcheck=\"false\" />\n\t\t\t</NcFormBox>\n\n\t\t\t<NcButton\n\t\t\t\ttype=\"submit\"\n\t\t\t\tvariant=\"primary\"\n\t\t\t\twide>\n\t\t\t\t{{ t('settings', 'Change password') }}\n\t\t\t</NcButton>\n\t\t</form>\n\t</NcSettingsSection>\n</template>\n\n<style module>\n.passwordSection__form {\n\tdisplay: flex;\n\tflex-direction: column;\n\tgap: calc(2 * var(--default-grid-baseline));\n\tmax-width: 300px !important;\n}\n</style>\n"],"sourceRoot":""}]);
